import React, { useState } from "react";
import Order from "./contact.jsx";
import Locations from "./locations.jsx";
import Pride from "./pride.jsx";
import Menu from "./menu.jsx";
import cards, { pride, menu, location } from "./catchy.jsx";
import "./styles.css";
import { useNavigate } from "react-router-dom";

const forscroll = {
  behaviour: "smooth"
};
const t = {
  width: "70%",
  fontSize: "25px",
  opacity: "0.8"
};

function Catch(props) {
  return (
    <div class="card" style={t}>
      <div class="card-body">
        <h1 class="card-title">{props.heading}</h1>
        <br />
        <p class="card-text">{props.paragraph}</p>
      </div>
    </div>
  );
}
export default function Farm() {
  const [tog, setog] = useState(true);
  function Toggle() {
    if (tog === false) {
      setog(true);
    } else {
      setog(false);
    }
  }
  const navi = useNavigate();
  function handleclick() {
    navi("./reserve.jsx");
  }
  const handleClickScroll = () => {
    const element = document.getElementById("dess");
    if (element) {
      element.scrollIntoView({ forscroll });
    }
  };
  const handleClickScroll2 = () => {
    const element = document.getElementById("menu");
    if (element) {
      element.scrollIntoView({ forscroll });
    }
  };
  const handleClickScroll3 = () => {
    const element = document.getElementById("explore");
    if (element) {
      element.scrollIntoView({ forscroll });
    }
  };
  const handleClickScroll4 = () => {
    const element = document.getElementById("ord");
    if (element) {
      element.scrollIntoView({ forscroll });
    }
  };
  return (
    <div className="Head">
      <img id="logo" src={require("./images/FarmRetreat.jpg")} alt="hi" />
      <div className="first">
        <div id="front">
          <h1 id="explore"> EXPLORE US </h1>
          <img
            id="ham"
            onClick={Toggle}
            src={
              "data:image/png;base64,"
            }
            alt="r"
          />
          <div id="home" className={tog ? "nav-bar" : "nav"}>
            <p onClick={handleClickScroll3}>Home</p>
            <p onClick={handleClickScroll}>About Us</p>
            <p onClick={handleClickScroll2}>Menu</p>
            <p onClick={handleClickScroll3}>Order Online</p>
            <p onClick={handleClickScroll4}>Contact us</p>
          </div>
          <center>
            <button class="order" onClick={handleclick}>
              Book your Table
            </button>
          </center>
        </div>
      </div>
      <div className="catch">
        <p id="dess">{cards.map(Catch)}</p>
        <p id="prid"> {pride.map(Pride)} </p>
        <p id="menu">{menu.map(Menu)}</p>
      </div>
      <div id="tr">
        <p id="loc"> {location.map(Locations)} </p>
      </div>
      <div className="footer">
        <p id="ord">
          <Order />
        </p>
      </div>
    </div>
  );
}
