import React, { useState } from "react";

const t = {
  // height: '100%',
  marginTop: "20%",
  fontSize: "20px",
  opacity: "0.8"
};
const available = {
  opacity: "0.7",
  color: "white"
};
const countries = [
  {
    Country: "India",
    Merch: [
      "Nature Villa Street,Near Chennai Central ,Chennai",
      "New Big Bazaar street ,Electronic City,Bangalore",
      "The Newton Grand Street, Thane,Mumbai"
    ],
    Image1: require("./images/chennai1.jpg"),
    Image2: require("./images/bangalore.jfif"),
    Image3: require("./images/mumbai.jfif")
  },
  {
    Country: "Sri Lanka",
    Merch: [
      "Austin Avenue,Neuwarilya ,Colombo",
      "Stupa Street ,Kandy",
      "Seyyon Avenue ,Nallur"
    ],
    Image1: require("./images/srilanka1.jfif"),
    Image2: require("./images/srilanka2.jfif"),
    Image3: require("./images/srilanka3.jfif")
  },
  {
    Country: "United States",
    Merch: [
      "The Paris Corner ,New York City",
      "West Obama Street ,Washington DC",
      "Dubai Main Street ,New Jersey"
    ],
    Image1: require("./images/us1.jfif"),
    Image2: require("./images/us2.jfif"),
    Image3: require("./images/us3.jfif")
  }
];

function Locations(props) {
  const [name, setname] = useState("");
  const [state, setstate] = useState("");
  function Name(event) {
    setname(event.target.value);
  }
  function Set() {
    if (name === "") {
      setname("");
    } else {
      setstate(
        countries
          .filter((val) => {
            if (val.Country.toLowerCase() === name.toLowerCase()) {
              return val;
            } else if (name === "") {
              return "";
            }
          })
          .map((val) => {
            return (
              <div className="map">
                <center>
                  <img src={val.Image1} alt="hi" />
                  <p>{val.Merch[0]}</p>
                  <img src={val.Image2} alt="bi" />
                  <p>{val.Merch[1]} </p>
                  <img src={val.Image3} alt="dai" />
                  <p>{val.Merch[2]}</p>
                </center>
              </div>
            );
          })
      );
    }
  }
  return (
    <div>
      <div class="card mb-3" style={t}>
        <div class="row g-0">
          <div class="col-md-4">
            <img
              id="locat"
              src={props.image}
              class="img-fluid rounded-start"
              alt="pl"
            />
          </div>
          <div class="col-md-8">
            <div class="card-body">
              <h2 class="card-title">{props.heading}</h2>
              <p class="card-text">{props.paragraph}</p>
              <div style={available}>
                <h3>Available Outlets</h3>
                <ul>
                  <li>India</li>
                  <li>Sri Lanka</li>
                  <li>United States</li>
                </ul>
              </div>
              <div>
                <form class="d-flex">
                  <input
                    onChange={Name}
                    class="form-control me-2"
                    type="search"
                    placeholder="Search for our outlets"
                    aria-label="Search"
                  />
                </form>
                <div>
                  <button onClick={Set} class="btn btn-outline-success">
                    Search
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <p className="merch">{state}</p>
    </div>
  );
}

export default Locations;
