import React from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function Reserve() {
  const [name, setname] = useState("");
  function clicked() {
    setname("Registered Successfully!!!");
  }
  const navi = useNavigate();
  function handleclick() {
    navi("/");
  }
  return (
    <div className="resu">
      <h1 style={{ color: "gold" }}>Come enjoy our Delicacies!</h1>
      <br />
      <h1 style={{ color: "gold" }}>Book Now !</h1>
      <form>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputEmail4">Name</label>
            <input type="text" class="form-control" id="inputEmail4" />
          </div>
          <div class="form-group col-md-6">
            <label for="inputPassword4">Email</label>
            <input type="email" class="form-control" id="inputPassword4" />
          </div>
        </div>
        <div class="form-group">
          <label for="inputAddress">Address</label>
          <input
            type="text"
            class="form-control"
            id="inputAddress"
            placeholder="1234 Main St"
          />
        </div>
        <div class="form-group col-md-6">
          <label for="inputEmail4">Contact number</label>
          <input
            type="text"
            class="form-control"
            id="inputEmail4"
            placeholder="+91 for India..."
          />
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputCity">City</label>
            <input type="text" class="form-control" id="inputCity" />
          </div>
          <div class="form-group col-md-2">
            <label for="inputZip">Zip</label>
            <input type="text" class="form-control" id="inputZip" />
          </div>
        </div>
        <div class="form-group">
          <div class="form-check">
            <input class="form-check-input" type="checkbox" id="gridCheck" />
            <label class="form-check-label" for="gridCheck">
              Send me the notification
            </label>
          </div>
        </div>
      </form>
      <button class="btn btn-primary" onClick={clicked}>
        Register your seat Now
      </button>
      <button id="button2" class="btn btn-primary" onClick={handleclick}>
        Home
      </button>
      <p
        style={{
          color: "white",
          marginLeft: "30%",
          marginBottom: "10%",
          fontSize: "50px"
        }}
      >
        {name}
      </p>
    </div>
  );
}
