import React, { useState } from "react";

const y = {
  color: "gold",
  opacity: "0.8",
  fontSize: "20px",
  border: "2px solid gold",
  borderRadius: "7px"
};

function Menu(props) {
  const [state, setstate] = useState(false);
  function Menushow() {
    if (state === false) {
      const t = <img src={require("./images/Menu.jpg")} alt="yi" />;
      setstate(t);
    } else {
      setstate(false);
    }
  }
  return (
    <div className="men">
      <div class="card" id="newmenu">
        <div class="card-body">
          <center>
            <h1 class="card-title">{props.heading}</h1>
            <br />
            <p class="card-text">{props.paragraph}</p>
            <button
              id="delicacy"
              style={y}
              onClick={Menushow}
              class="btn btn-outline-success"
            >
              Click here to see our Delicacies
            </button>
          </center>
        </div>
      </div>
      <center>
        <div className="menucard">{state}</div>
      </center>
    </div>
  );
}

export default Menu;
