import React from "react";

export default function Pride(props) {
  return (
    <div class="card .bg-transparent" id="pridet">
      <div class="card-body">
        <h1 class="card-title">{props.heading}</h1>
        <br />
        <p class="card-text">{props.paragraph}</p>
      </div>
    </div>
  );
}
